                        <p class="txt" v-show="idx==1">당장 일상생활에서 쓸 근육도 부족한 당신!<br>대출이 필요하신가요?<br>마음이 급해 당장 닭가슴살부터 당겨쓰는 당신!</p>
                        <p class="txt" v-show="idx==2">바쁜 일상속 운동까지 틈틈이 하기는 매우 어렵습니다.<br>마음은 있지만 체력이나 시간이 부족하여, 해야 하는 걸<br>알지만 못하는 경우가 많습니다. 그러다 보면<br>운동이 멀게 느껴지고 재미없고 부담스러울 수 있습니다.</p>
                        <p class="txt" v-show="idx==3">주 2회 근력운동을 꾸준히 하면 보다 더 건강한 몸이<br>될 수 있습니다.</p>
                        <p class="txt" v-show="idx==4">우리 몸에는 골격과 골격을 지탱해 주는 근육이 있습니다.<br>우리의 근육은 초과되지도 단축되지도 않는 정상길이를<br>유지할 때 최고의 근력을 발휘합니다.</p>
