                    </li>
                    <li class="item">
                        <p class="txt" v-show="idx==1">근육이 부족한 원인은 다양하지만 대표적으로는<br>탄수화물 위주의 식습관과 운동 부족이 많습니다.</p>
                        <p class="txt" v-show="idx==2">고강도의 운동은 운동 전에 먹은 탄수화물을 연소 시키지만,<br>내가 원하는 뱃살과 허벅지의 지방은 빠지지 않습니다.<br>특히 과도한 운동을 지속하면 몸 속에 저장된 탄수화물을<br>전부 사용하고 이후 근육의 단백질이 소모되기 때문에<br>오히려 근손실이 오게 됩니다.</p>
                        <p class="txt" v-show="idx==3">근육량이 많은 사람일 수록 근육을 유지하기가 어려워<br>근육 손실하는 시간이 빠른 편입니다.</p>
                        <p class="txt" v-show="idx==4">내가 원하는 부위의 운동만 지속적으로 하게 되면,<br>질기고 단단해진 근육이 상대적으로 약한 근육을<br>끌어당기게 되고, 결국 자세가 틀어지는 불균형이<br>오게 되어 신경통증으로 이어질 수 있습니다. <br>(예를 들어, 과도한 복근 운동만 하게 될 경우 등이 굽게<br>될 수 있습니다.)</p>
                    </li>
                    <li class="item" v-if="idx!=4">
                        <p class="txt" v-show="idx==1">단백질 섭취량이 많을수록 근육이 부족한 칼로리를<br>대체하고 유지하는 데 도움이 되고, 체지방 감소에<br>효과적입니다. (과도한 단백질 섭취는 신장에 해가<br>되어 개개인의 몸무게 별 일일 권장량을 섭취하시길<br>제안합니다.)</p>
                        <p class="txt" v-show="idx==2">그렇다면 적당히 하기 쉽고 기분을 상쾌하게<br>만들어 주는 가벼운 운동을 시작하는 건 어떨까요?</p>
                        <p class="txt" v-show="idx==3">일반인의 경우 1주일 정도 운동을 하지 않으면 근손실이<br>시작되며, 평소보다 활동량이 급격히 줄어든 상태로<br>10일을 보내면 근육의 크기가 그 이전보다 11% 정도<br>감소합니다.</p>
                    </li>
                    <li class="item" v-if="idx==3">
                        <p class="txt" v-show="idx==3">특히 공복에 하는 고강도 웨이트 운동은 근손실을<br>유발하기 때문에 가장 중요한 것은 운동의 강도입니다.</p>
