                    <li class="item">
                        <img src="@/assets/result/img1/img3.png" v-show="idx==1">
                        <img src="@/assets/result/img2/img3.png" v-show="idx==2">
                        <img src="@/assets/result/img3/img3.png" v-show="idx==3">
                        <img src="@/assets/result/img4/img3.png" v-show="idx==4">
                    </li>
                </ul>
            </div>
            <div class="txt_list_area">
                <ul class="list">
                    <li class="item">
                        <p class="txt" v-show="idx==1">동물복지 또는 무항생제 인증을 받은 닭 가슴살이나<br>달걀 또는 스테이크와 같은 음식을 꾸준히 섭취하고,<br>과도한 동물성 단백질 섭취로 인한 유당 부작용,<br>호르몬 불균형이 생기는 경우는 식물성 단백질<br>섭취를 꾸준히 하는 게 좋습니다.</p>
                        <p class="txt" v-show="idx==2">산책하기 / 자전거 타기 / 러닝 / 등산 등</p>
                        <p class="txt" v-show="idx==3">고강도의 운동으로 탄수화물이 다 쓰이고 나면<br>단백질이 쓰이기 때문에 오히려 근손실이 오게<br>됨으로 주 1-2회 유산소를 병행하는 중저 강도의<br>근력운동을 꾸준히 습관화 하면 원하는 몸의 라인을<br>만들 수 있습니다.</p>
                        <p class="txt" v-show="idx==4">근육 초부자님은 근육을 평소보다 더 사용해야 하는<br>활동 전후로 균형적인 스트레칭을 통해(근막이완) 근육의<br>길이가 정상 길이로 유지될 수 있게 풀어주는 것이 도움이<br>됩니다. 단 엉덩이 근육은 무제한 과활성 되어도 되는<br>근육으로 꾸준한 운동을 하는 것이 좋습니다.</p>
                    </li>
                    <li class="item" v-if="idx!=3 && idx!=4">
                        <p class="txt" v-show="idx==1">따로 시간을 내어 운동하기보다 일상생활에서<br>장시간 앉아있지 않고 한 시간에 한 번씩 일어나기<br>또는 물건을 들어 올릴 때 허리나 팔의 관절로 올리는<br>것이 아니라 엉덩이와 허벅지의 힘으로 일어서면서<br>물건 올리기와 같이 일상생활 속에서 근육을 인지하고<br>사용하면 근육 거지에서 벗어날 수 있습니다.</p>
                        <p class="txt" v-show="idx==2">하루 40분 이상 가벼운 산책을 하는 것도 좋은 방법입니다. </p>
                    </li>
                </ul>
            </div>
            <div class="detail_txt_area">
