</template>

<script>
import $ from 'jquery'
export default {
    name:"indexView",
    data() {
        return {
            idx:0
        }
    },
    methods:{
        prev:function(){
            this.$router.push('/result_choice');
        },
        home:function(){
            this.$router.push('/');
        },
        wordsquare:function(){
            window.open("https://www.wordsquare.org/");
        },
        share_fun: async function(){
            try {
                await navigator.share({
                    title: "근질테스트",
                    url: "",
                });
                console.log("공유 성공");
            } catch (e) {
                var ans=confirm('url 복사하시겠습니까?');
            if(ans){
                var url = '';
                    var textarea = document.createElement("textarea");
                    document.body.appendChild(textarea);
                    url = location.origin+location.pathname;
                    textarea.value = url;
                    textarea.select();
                    document.execCommand("copy");
                    document.body.removeChild(textarea);
                }
            }
        },
        copy:function(){
            var url = '';
            var textarea = document.createElement("textarea");
            document.body.appendChild(textarea);
            url = location.origin+location.pathname;
            textarea.value = url;
            textarea.select();
            document.execCommand("copy");
            document.body.removeChild(textarea);
        },
        detail_choice:function(){
            this.$router.push('/detail_choice');
        }
    },
    mounted() {
        var idx = this.$route.query.idx;
        this.idx=idx;
        if(!(idx==1 || idx==2 || idx==3 || idx==4)){
            alert('비정상적인 접근입니다.');
            this.$router.push('/result_choice');
        }
        var default_on=$('.music_area>.music_icon').find('.icon_on');
        var default_off=$('.music_area>.music_icon').find('.icon_off');
        
        var video = document.getElementById('audio_id'); 
        var is_play=video.paused;
        if(is_play){
            default_on.hide();
            default_off.hide();
            default_off.css({
                display:'block'
            });
        }else{
            default_on.hide();
            default_off.hide();
            default_on.css({
                display:'block'
            });
        }
        $('.music_area>.music_icon').click(function(){
            var that = $(this);
            console.log(that);
            var on = that.find('.icon_on');
            var off = that.find('.icon_off');
            var on_status = on.css('display');
            var off_status = off.css('display');
            if(on_status=='none' && off_status=='block'){
                on.hide();
                off.hide();
                on.css({
                    display:'block'
                });
                document.getElementById('audio_id').play();
            }else if(on_status=='block' && off_status=='none'){
                on.hide();
                off.hide();
                off.css({
                    display:'block'
                });
                document.getElementById('audio_id').pause();
            }
        });
        setTimeout(() => {
            $('.tit_txt_area').addClass('on');
        }, 500);

        $(window).scroll(function(){
            var body_height = parseInt($('html').css('height'));
            var win_vh = $(window).height();
            var win_vh2 = body_height-win_vh;

            let st = $(window).scrollTop();
            var stPercent = parseInt((st/win_vh2)*100);
            
            if(stPercent>=40){
                setTimeout(() => {
                    $('.segment_img>.list>.item').eq(0).addClass('on');
                }, 250);
                setTimeout(() => {
                    $('.segment_img>.list>.item').eq(1).addClass('on');
                }, 500);
                setTimeout(() => {
                    $('.segment_img>.list>.item').eq(2).addClass('on');
                }, 750);
            }
           
        })
    },
}
</script>

